header {
    background-color: var(--color-neutral4);
    display: flex;
    position: fixed;
    align-items: center;
    z-index: 1000;
    width: 100vw;
}

header > :first-child {
    margin-right: auto;
}

header > :last-child {
    display: none;
}

nav {
    display: none;
}

nav:before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: var(--color-neutral1);
    opacity: 0.6;
}

nav > :first-child {
    position: absolute;
    top: 1.5rem;
    left: 1.5rem;
    right: 1.5rem;
    background-color: var(--color-neutral4);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 5px;
}

.showNav {
    display: flex;
    flex-direction: column;
    position: fixed;
    left: 0;
    top: 4.4rem;
    width: 100%;
    height: 100vh;
    background-color: transparent;
}

.intro {
    background-color: var(--color-neutral3);
}

.intro > :first-child {
    position: relative;
    text-align: center;
    background: url("../assets/images/bg-intro-mobile.svg");
    background-repeat: no-repeat;
    background-position: top center;
    background-size: cover;
}

.intro > :first-child> :only-child {
    position: relative;
    max-width: 85%;
    top: -1.5rem;
    bottom: 0;
}

.intro > :nth-child(2) {
    color: var(--color-primary1);
}

.intro p {
    text-align: justify;
} 

.request__invite {
    background-color: var(--color-primary2);
    border-radius: 3rem;
    width: 8rem;
    padding: 1rem;
    font-size: var(--font-size-small);
    font-weight: 400;
    color: var(--color-neutral4);
}

.why__us {
    color: var(--color-primary1);
    background-color: var(--color-neutral2);
}

.latest__article * :not(h1) {
    background-color: var(--color-neutral4);
    font-size: var(--font-size-smaller);
}

.latest__article h2 {
    font-size: var(--font-size-small);
}

.latest__article > :first-child {
    margin-top: calc(var(--space) * 3);
}

.latest__article > :last-child * {
    text-align: left;
    background-color: var(--color-neutral3);
}

.card > img {
    max-width: 100%;
}

footer {
    background-color: var(--color-primary1);
    color: var(--color-neutral2);
}

footer svg :nth-child(2) :first-child {  /*I don't know much about svg, so this is kinda crappy*/
    fill: white;
}

footer p {
    font-size: var(--font-size-small);
}

@media screen and (min-width: 550px) {
    header .stack {
        --stack-space: 0;
    }

    header[class^="box"], header .box {
        --box-space: 0.5rem;
    }

    header > :first-child {
        margin-left: 7%;
    }
    
    header > :last-child {
        margin-left: auto;
        margin-right: 7%;
        display: block;
    }

    header > :nth-child(3) * {
        font-size: var(--font-size-small);
    }

    header > :nth-child(3) * + * {
        margin-left: 1rem;
    }

    header > :nth-child(2) {
        display: none;
    }

    nav {
        display: block;
    }
    
    nav:before {
        display: none;
    }
    
    nav > :first-child {
        position: relative;
        top: 0;
        left: 0;
        right: 0;
        background-color: transparent;
        display: flex;
        flex-direction: row;
        align-items: center;
        border-radius: 0px;
    }

    .request__invite {
        padding: 0.7rem;
    }

    .intro {
        display: flex;
        flex-direction: row-reverse; 
    }

    .intro .box {
        --box-space: 0%;
    }

    .intro > :first-child {
        background: url("../assets/images/bg-intro-desktop.svg");
        height: 100%;
        background-size: cover;
        background-repeat: no-repeat;
    }
    
    .intro > :first-child> :only-child {
        max-width: 100%;
    }
    
    .intro > :nth-child(2) {
        color: var(--color-primary1);
        margin-left: 7%;
        margin-top: auto;
        margin-bottom: auto;
    }
    
    .intro p {
        text-align: left;
    }

    .why__us > *, .latest__article > *, footer > * {
        margin-left: calc(7% - 1rem);
        margin-right: calc(7% - 1rem);
    }

    .why__us *, .latest__article *, footer * {
        text-align: left;
    }

    footer {
        display: flex;
    }

    footer > :last-child {
        margin-left: auto;
        display: flex;
        flex-direction: column;
        align-items: flex-end;
    }

    footer > :last-child > * {
        text-align: center;
    }
}